import React, { Component } from 'react';
import './Deck.css';
import IndexCard from '../card/IndexCard';
import DeckTitle from './DeckTitle';
import { CardDeck } from 'reactstrap';
class IndexDeck extends Component {
  state = {};
  render() {
    return (
      <div className="deck-c">
        <DeckTitle title="学习入口" />
        <CardDeck>
          <IndexCard
            title="活动中心"
            subTitle="online-learning"
            text="活动报名，线下培训，活动查询。"
            to="/plateform"
            alt="card image cap"
          />
          <IndexCard
            title="技术中心"
            subTitle="technology"
            text="技术，分享与学习的课堂"
            to="/technology"
            alt="card image cap"
          />
          <IndexCard
            title="UED学院"
            subTitle="用户体验设计 User Experience Design"
            text="设计师们的殿堂"
            to="/ued"
            alt="card image cap"
          />
        </CardDeck>
      </div>
    );
  }
}

export default IndexDeck;



