import React, { Component } from 'react';
import './Deck.css';
import { Link } from 'react-router-dom';
class DeckTitle extends Component {
  state = {};
  render() {
    let that = this;
    const hasRightBt = () => {
      if (that.props.bTitle) {
        if (this.props.to) {
          return (
            <Link className="deck-title-tab-rank" to={this.props.to}>
              {this.props.bTitle}
            </Link>
          );
        } else {
          return (
            <span className="deck-title-tab-rank">{this.props.bTitle}</span>
          );
        }
      }
      return <div />;
    };
    return (
      <div className="deck-title-h">
        <span className="h3">{this.props.title}</span>
        {hasRightBt()}
      </div>
    );
  }
}

export default DeckTitle;



