import React, {Component} from 'react';
import {
  Button,
  CardImg,
  CardTitle,
  CardText,
  Jumbotron,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter
} from 'reactstrap';
import {Row, Col} from 'reactstrap';
import {Train} from '../Api';
import {css} from 'react-emotion'
const content = css `
// width:60%;
margin: 0 auto;
padding:2%
max-width:1000px;

background: rgba(222, 221, 221, 0.8);
& button{
    padding: 10px 37px;
    color: #cac8c8;
    border-radius: 30px;
    border: 0px;
    background: #303837;
    cursor: pointer;
}

`

class TrainDetail extends Component {
  constructor(props) {
    super(props);
    this.switch = this
      .switch
      .bind(this)
    this.toggle = this
      .toggle
      .bind(this)

    let id = this.props.match.params.id;
    let detail = Train.get(id);
    this.state = {
      detail: detail,
      status: false,
      modal: false
    };
  }
  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  }
  switch (e) {
      this.setState({
        status: !this.state.status
      });
      this.toggle();    
  }
  state = {};
  render() {
    let detail = Train.get(this.props.match.params.id);
    const Model = () => <Modal
      isOpen={this.state.modal}
      toggle={this.toggle}
      className={this.props.className}>
      <ModalHeader className="text-justify" toggle={this.toggle}>确认{this.state.status && '取消' }报名</ModalHeader>
      <ModalBody>
          确认{this.state.status && '取消' }参加 <u>王善勇科学家</u>的行星博览？
          </ModalBody>
      <ModalFooter>
        <Button color="primary" onClick={this.switch}>确认</Button>{' '}
        <Button color="danger" onClick={this.toggle}>取消</Button>
      </ModalFooter>
    </Modal>
    return (
      <div className={content}>
        <div className="pb-3 pl-0 pr-0">
          <Row>
            <Col xs="5">
              <CardImg bottom height="240" width="100%" src={detail.imgUrl} alt={detail.alt}/>
            </Col>
            <Col xs="7">
              <div className="p-3">
                <CardTitle className="text-justify">
                  {detail.title}
                </CardTitle>
                <CardText className="text-justify">
                  {detail.time}
                </CardText>
                <CardText className="text-justify">
                  {detail.place}
                </CardText>
                <CardText className="text-justify">
                  {detail.timeEnronlStop}
                </CardText>
                <CardText className="text-justify">
                  <button onClick={this.toggle}>{this.state.status
                      ? "取消报名"
                      : "立即报名"}</button>
                  <Model/>
                </CardText>
              </div>
            </Col>
          </Row>
        </div>
        <Jumbotron>

          <h1 className="display-3">Domain Driven Design</h1>
          <p className="lead">2003年，Domain Driven Design
            一书面世。这一架构思想深深的影响了过去15年软件行业的架构理论和实践 体系的发展路径。依赖反转、洋葱圈架构、六边形架构、CQRS架构、EDA架构、到Uncle
            Bob提出的整洁架构原则、以及2014年 Martin Fowler和James
            Lewis提出的微服务架构风格，这些不同的架构思想的出现，都显示出领域驱动设计（DDD）的思想越 来越深入这个行业，也越来越深入产品以及企业IT架构设计本身。</p>
          <hr className="my-2"/>
          <img height="240" width="100%" src={detail.imgUrl} alt={detail.alt}/>
          <p>It uses utility classes for typgraphy and spacing to space content out within
            the larger container.</p>
          <p className="lead">2003年，Domain Driven Design
            一书面世。这一架构思想深深的影响了过去15年软件行业的架构理论和实践 体系的发展路径。依赖反转、洋葱圈架构、六边形架构、CQRS架构、EDA架构、到Uncle
            Bob提出的整洁架构原则、以及2014年 Martin Fowler和James
            Lewis提出的微服务架构风格，这些不同的架构思想的出现，都显示出领域驱动设计（DDD）的思想越 来越深入这个行业，也越来越深入产品以及企业IT架构设计本身。</p>
          <p className="lead">2003年，Domain Driven Design
            一书面世。这一架构思想深深的影响了过去15年软件行业的架构理论和实践 体系的发展路径。依赖反转、洋葱圈架构、六边形架构、CQRS架构、EDA架构、到Uncle
            Bob提出的整洁架构原则、以及2014年 Martin Fowler和James
            Lewis提出的微服务架构风格，这些不同的架构思想的出现，都显示出领域驱动设计（DDD）的思想越 来越深入这个行业，也越来越深入产品以及企业IT架构设计本身。</p>
          <p className="lead">2003年，Domain Driven Design
            一书面世。这一架构思想深深的影响了过去15年软件行业的架构理论和实践 体系的发展路径。依赖反转、洋葱圈架构、六边形架构、CQRS架构、EDA架构、到Uncle
            Bob提出的整洁架构原则、以及2014年 Martin Fowler和James
            Lewis提出的微服务架构风格，这些不同的架构思想的出现，都显示出领域驱动设计（DDD）的思想越 来越深入这个行业，也越来越深入产品以及企业IT架构设计本身。</p>
        </Jumbotron>
      </div>

    );
  }
}

export default TrainDetail;



