/* eslint react/no-multi-comp: 0, react/prop-types: 0 */
import React from 'react';
import {Tooltip} from 'reactstrap';
import FaCircle from 'react-icons/lib/fa/circle'
import {css} from 'react-emotion'
const RedCircle = css `
color:orange;
font-size:12px;
`
const Red = css `
    color: orange;
    font-size: 10px;
    height: 10px;
`
const whitebg = css `
 .tooltip-inner{
  background-color: #ececec;  
}
`
export default class TileTooltip extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this
      .toggle
      .bind(this);
    this.state = {
      tooltipOpen: false,
      content: this.props.content ? this.props.content : ['WEDATA提数培训!', '后台培训', '新员工入门培训']
    };
  }

  toggle() {
    this.setState({
      tooltipOpen: !this.state.tooltipOpen
    });
  }

  render() {
    return (
      <div className={Red} id={"TileTooltip" + this.props.tid}><FaCircle/>
        <Tooltip
          className={whitebg}
          placement="bottom"
          isOpen={this.state.tooltipOpen}
          target={"TileTooltip" + this.props.tid}
          toggle={this.toggle}>
          {this
            .state
            .content
            .map(a => <div className={RedCircle+" text-justify"}>
              ·{a}
            </div>)}
        </Tooltip>
      </div>
    );
  }
}


