import React, { Component } from 'react';
import { Card, CardImg } from 'reactstrap';
import { withRouter } from 'react-router-dom';
import './Card.css';

class NewsCard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgUrl: this.props.imgUrl
        ? this.props.imgUrl
        : 'https://placeholdit.imgix.net/~text?txtsize=33&txt=318%c3%97180&w=318&h=222',
      color: this.props.initialColor
    };
  }
  state = {};
  render() {
    let that = this;
    const B = withRouter(({ history }) => (
      <Card
        className="fa-car video-thumbnail quality-card mt-3 mb-3"
        onClick={() => history.push(that.props.to)}
      >
        <CardImg
          top
          width="100%"
          src={that.state.imgUrl}
          alt={that.state.alt}
        />
        <div className="float-title " style={{ textAlign: 'left' }}>
          {that.props.title}
        </div>
      </Card>
    ));

    return (
      <div>
        <B />
      </div>
    );
  }
}

export default NewsCard;



