import React, {Component} from 'react';
import styled, {css} from 'react-emotion'
import {Link} from 'react-router-dom';


const cLeft = css`
text-align: left;
margin: 10px 0px;
`
const breadCrumbs = css`
color: #56707f;
font-size: 12px;
font-family: "Motiva Sans",
Sans-serif;
font-weight: normal;
padding-top: 10px;
`
const breadCrumbsA = css`
color: #8f98a0;
`
const blockbg = css`
`
const header = css`
height: 40px;
line-height: 40px;
font-size: 30px;
color: #d6d7d8;
`
class PageTitle extends Component {
  state = {}
  render() {
    return (
      <div className={cLeft}>
        <div className={breadCrumbs}>
          <div className={blockbg}>
            <Link className={breadCrumbsA} to="/">主页</Link>
          </div>
          <div/>
        </div>
        <span className={header}>{this.props.title}</span>
      </div>
    );
  }
}

export default PageTitle;


