import React, { Component } from 'react';
import {
  Card,
  Button,
  CardImg,
  CardTitle,
  CardText,
  CardSubtitle,
  CardBody
} from 'reactstrap';
import { withRouter } from 'react-router-dom';

class IndexCard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgUrl: this.props.imgUrl
        ? this.props.imgUrl
        : 'https://placeholdit.imgix.net/~text?txtsize=33&txt=318%c3%97180&w=318&h=180',
      color: this.props.initialColor
    };
  }
  state = {};
  render() {
    let that = this;
    const B = withRouter(({ history }) => (
      <Card>
        <CardImg
          top
          width="100%"
          src={that.state.imgUrl}
          alt={that.state.alt}
        />
        <CardBody>
          <CardTitle>{that.props.title}</CardTitle>
          <CardSubtitle>{that.props.subTitle}</CardSubtitle>
          <CardText>{that.props.text}</CardText>
          <Button onClick={() => history.push(that.props.to)}>
            进入{that.props.title}
          </Button>
        </CardBody>
      </Card>
    ));
    return <B />;
  }
}

export default IndexCard;



