import React, { Component } from 'react';
import {
  Button,
  FormGroup,
  Label,
  InputGroupAddon,
  Input,
  InputGroup,
  Col,
  FormText
} from 'reactstrap';
import './Registry.css';
import { Link } from 'react-router-dom';
import FaCommentingO from 'react-icons/lib/fa/commenting-o';
import {css} from 'react-emotion'
const vdisable = css`
background: 6c6d6d !important;
width: 60px;  
& svg{
  display:none
}
&::after{
  content:'60s'
}
`
const venable = css`
cursor:pointer;
background: linear-gradient(0deg, #020802, #2c2cb8 40%, #020802) !important; 
`
class Reigstry extends Component {
  constructor(props) {
    super(props);
    this.state = {
      vcode:true,
    };
    this.toggle = this
      .toggle
      .bind(this);
  }

  toggle(){
    if(!this.state.vcode){
      return
    }
    this.setState({vcode:!this.state.vcode})
    let that = this;
    setTimeout(function() {
      that.setState({vcode:!that.state.vcode})
    }, 60 * 1000);
  }
  render() {
    return (
      <div className="registry-content">
        <div className="cleft">
          <div className="breadcrumbs">
            <div className="blockbg">
              <Link to="/">主页</Link>
            </div>
            <div />
          </div>
          <span className="registry-header">创建账号</span>
        </div>
        <div className="registry-form-content">
          <FormGroup row>
            <Label for="username" className="p-1" sm={3}>
              昵称
            </Label>
            <Col sm={9}>
              <InputGroup>
                <Input name="username" placeholder="昵称" />
              </InputGroup>
              <FormText>建议使用中文！4-16字符</FormText>
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label for="mobile" sm={3}>
              手机号
            </Label>
            <Col sm={9}>
              <InputGroup>
                <Input name="mobile" placeholder="请输入手机号" />
              </InputGroup>
              <FormText>仅支持中国大陆用户.</FormText>
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label for="vcode" sm={3}>
              验证码
            </Label>
            <Col sm={9}>
              <InputGroup>
                <Input name="vcode" placeholder="发送短信验证码" />
                <InputGroupAddon onClick={this.toggle} className={"  "+ (this.state.vcode ? venable : vdisable)} sm={2}>
                  <FaCommentingO />
                </InputGroupAddon>
              </InputGroup>
              <FormText>输入4位手机验证码.</FormText>
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label for="email" sm={3}>
              邮箱
            </Label>
            <Col sm={9}>
              <InputGroup>
                <Input name="email" placeholder="请输入邮箱地址" />
              </InputGroup>
              <FormText>挂号网邮箱.</FormText>
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label for="email" sm={3}>
              密码
            </Label>
            <Col sm={9}>
              <InputGroup>
                <Input type="password" name="password" placeholder="password" />
              </InputGroup>
              <FormText>至少8位 至少一个特殊字符，一个数字</FormText>
            </Col>
          </FormGroup>
          <FormGroup row>
            <Label for="email" sm={3}>
              确认密码
            </Label>
            <Col sm={9}>
              <InputGroup>
                <Input type="password" name="password" placeholder="password" />
              </InputGroup>
              <FormText>至少8位 至少一个特殊字符，一个数字</FormText>
            </Col>
          </FormGroup>
          <FormGroup check row>
            <Col
              sm={{
                size: 5,
                offset: 2
              }}
            >
              <Button>注册</Button>
            </Col>
          </FormGroup>
        </div>
      </div>
    );
  }
}

export default Reigstry;



