import React, {Component} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap';
import {css} from 'react-emotion'
import {Auth} from './Auth'
const radius = css `
border-radius:50%;
`
const ToIndex = withRouter(({history}) => (
  <NavbarBrand
    href="#  "
    className="text-muted"
    onClick={() => history.push('/')}>
    星云
  </NavbarBrand>
));
class Header extends Component {
  constructor(props) {
    super(props);
    this.togglePlateform = this
      .togglePlateform
      .bind(this);
    this.toggleRight = this
      .toggleRight
      .bind(this);
    this.handleLogin = this
      .handleLogin
      .bind(this);
    this.handleLoginOut = this
      .handleLoginOut
      .bind(this);
    this.state = {
      isOpenRight: false,
      isOpenPlateform: false,
      auth: Auth
      // user:''
    };
  }

  toggleRight() {
    this.setState({
      isOpenRight: !this.state.isOpenRight
    });
  }
  togglePlateform() {
    this.setState({
      isOpenPlateform: !this.state.isOpenPlateform
    });
  }
  handleLogin(user) {
    this.setState({auth: Auth})
  }
  handleLoginOut() {}

  render() {
    let that = this;
    const Out = withRouter(({history}) => {
      return <div
        onClick={() => Auth.signout(() => {
        that.setState({auth: Auth});
        history.push("/login")
      })}>关于</div>
    })
    const IsLoggedIn = () => {
      return (
        <Nav className="ml-auto text-muted" navbar>
          <NavItem>
            <NavLink href="#">

              你好，{that.state.auth.username}

            </NavLink>
          </NavItem>
          <UncontrolledDropdown nav innavbar="true">
            <DropdownToggle nav caret>
              <Link to="/center">个人中心
                <img
                  height="24px"
                  data-v-7f1feead=""
                  src="https://wpimg.wallstcn.com/f778738c-e4f8-4870-b634-56703b4acafe.gif?imageView2/1/w/80/h/80"
                  class="user-avatar"
                  className={radius}/></Link>

            </DropdownToggle>
            <DropdownMenu>
              <DropdownItem>
                <Link to="/center/personal">
                  个人信息</Link>
              </DropdownItem>
              <DropdownItem>
                <Link to="/center/avtivityManager">
                  活动管理</Link>
              </DropdownItem>
              <DropdownItem divider/>
              <DropdownItem>
                <Out/>
              </DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>
        </Nav>
      );
    }
    const Page = () => {
      return (
        <Nav className="ml-auto" navbar>
          <UncontrolledDropdown
            nav
            isOpen={that.state.isOpenPlateform}
            toggle={that.togglePlateform}>
            <DropdownToggle nav caret>
              培训平台
            </DropdownToggle>
            <DropdownMenu>
              <DropdownItem>
                <Link to="/plateform">培训平台</Link>
              </DropdownItem>
              <DropdownItem>技术学院</DropdownItem>
              <DropdownItem>UED学院</DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>
        </Nav>
      );
    };
    return (
      <div className="header">
        <Navbar dark expand="md">
          <ToIndex/> {this.state.auth.isAuthenticated
            ? <Page/>
            : null}
          <NavbarToggler onClick={this.toggleRight}/>
          <Collapse isOpen={this.state.isOpenRight} navbar>
            {this.state.auth.isAuthenticated
              ? <IsLoggedIn/>
              : null}
          </Collapse>
        </Navbar>
      </div>
    );
  }
}
export default Header;


