import React, {Component} from 'react';
import Calendar from 'react-calendar';
import TileTooltip from './TileTooltip';

import {css} from 'react-emotion'

const bg =css`
& .react-calendar{
  background: rgba(232, 229, 229, 1)
}
`
export default class ActiviyCalendar extends Component {
  state = {
    date: new Date(),
    tooltipOpen: true,
    hasActivity: this.props.hasActivity
      ? this.props.hasActivity
      : () => true,
    getActivity: this.props.getActivity
      ? this.props.getActivity
      : () => null
  }

  onChange = date => {
    this.setState({date})
    if(this.props.onChange){
        this.props.onChange(date)
    }
  }
  tileContent = (date, view) => {
    return this.state.hasActivity && this
      .state
      .hasActivity(date)
      ? <TileTooltip
          tid={date
          .date
          .getTime(date.date)}
          content={this
          .state
          .getActivity(date.date)}/>
      : null
  }
  render() {
    return (
      <div className={bg}>
        <Calendar
          onChange={this.onChange}
          tileContent={this.tileContent}
          value={this.state.date}/>
      </div>
    );
  }
}


