import React, {Component} from 'react';
import {Jumbotron} from 'reactstrap';
import {News} from '../Api';
import {css} from 'react-emotion'
const content = css `
// width:60%;
max-width:1000px;

margin: 0 auto;
padding:2%
background: rgba(222, 221, 221, 0.8);
& button{
    padding: 10px 37px;
    color: #cac8c8;
    border-radius: 30px;
    border: 0px;
    background: #303837;
    cursor: pointer;
}

`

class NewsDetail extends Component {
  constructor(props) {
    super(props);
    this.switch = this
      .switch
      .bind(this)
    this.toggle = this
      .toggle
      .bind(this)

    let id = this.props.match.params.id;
    let detail = News.get(id);
    this.state = {
      detail: detail,
      status: false,
      modal: false
    };
  }
  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  }
  switch (e) {
      this.setState({
        status: !this.state.status
      });
      this.toggle();
  }
  state = {};
  render() {
    let detail = News.get(this.props.match.params.id);
    return (
      <div className={content}>
        <Jumbotron>

          <h1 className="display-4 text-justify">2017年微医新春年会华东场首曝</h1>
          <p className="pl-1 lead text-justify">应旭彬
            <small>2017.12.09</small>
          </p>
          <img height="240" width="100%" src={detail.imgUrl} alt={detail.alt}/>
          <p className="lead">2003年，Domain Driven Design
            一书面世。这一架构思想深深的影响了过去15年软件行业的架构理论和实践 体系的发展路径。依赖反转、洋葱圈架构、六边形架构、CQRS架构、EDA架构、到Uncle
            Bob提出的整洁架构原则、以及2014年 Martin Fowler和James
            Lewis提出的微服务架构风格，这些不同的架构思想的出现，都显示出领域驱动设计（DDD）的思想越 来越深入这个行业，也越来越深入产品以及企业IT架构设计本身。</p>
          <hr className="my-2"/>
          <img height="240" width="100%" src={detail.imgUrl} alt={detail.alt}/>
          <p>It uses utility classes for typgraphy and spacing to space content out within
            the larger container.</p>
          <p className="lead">2003年，Domain Driven Design
            一书面世。这一架构思想深深的影响了过去15年软件行业的架构理论和实践 体系的发展路径。依赖反转、洋葱圈架构、六边形架构、CQRS架构、EDA架构、到Uncle
            Bob提出的整洁架构原则、以及2014年 Martin Fowler和James
            Lewis提出的微服务架构风格，这些不同的架构思想的出现，都显示出领域驱动设计（DDD）的思想越 来越深入这个行业，也越来越深入产品以及企业IT架构设计本身。</p>
          <p className="lead">2003年，Domain Driven Design
            一书面世。这一架构思想深深的影响了过去15年软件行业的架构理论和实践 体系的发展路径。依赖反转、洋葱圈架构、六边形架构、CQRS架构、EDA架构、到Uncle
            Bob提出的整洁架构原则、以及2014年 Martin Fowler和James
            Lewis提出的微服务架构风格，这些不同的架构思想的出现，都显示出领域驱动设计（DDD）的思想越 来越深入这个行业，也越来越深入产品以及企业IT架构设计本身。</p>
          <p className="lead">2003年，Domain Driven Design
            一书面世。这一架构思想深深的影响了过去15年软件行业的架构理论和实践 体系的发展路径。依赖反转、洋葱圈架构、六边形架构、CQRS架构、EDA架构、到Uncle
            Bob提出的整洁架构原则、以及2014年 Martin Fowler和James
            Lewis提出的微服务架构风格，这些不同的架构思想的出现，都显示出领域驱动设计（DDD）的思想越 来越深入这个行业，也越来越深入产品以及企业IT架构设计本身。</p>
        </Jumbotron>
      </div>

    );
  }
}

export default NewsDetail;



