import React, { Component } from 'react';
import TrainDeck from '../deck/TrainDeck';
import NewsDeck from '../deck/NewsDeck';
import QualityDeck from '../deck/QualityDeck';
import LecturerDeck from '../deck/LecturerDeck';

import { Container, Row, Col } from 'reactstrap';

class Plateform extends Component {
  state = {};
  render() {
    return (
      <div>
        <Container>
          <Row>
            <Col>
              <TrainDeck/>
            </Col>
          </Row>
          <Row>
            <Col xs="8">
              <NewsDeck />
            </Col>
            <Col xs="4">
              <QualityDeck />
            </Col>
          </Row>
          <Row>
            <Col>
              <LecturerDeck />
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
export default Plateform;



