import React, {Component} from 'react';
import {css} from 'react-emotion'
import PropTypes from 'prop-types'
import {Switch, Route, withRouter} from 'react-router-dom';
import Personal from './Personal';
import ActivityManager from './ActivityManager';

const content = css `
display: flex;
margin: 0 auto;
// padding: 2%;
background: rgba(233, 236, 239,0.5);
height:100%;
& .jumbotron{
    background: rgba(233, 236, 239, 1)
}
`
const lf = css `
width:20%;
background: rgba( 111, 111, 111,0);

& div{
  cursor:pointer;
  padding: 5%;
  border-bottom:1px solid;
  color:#d6d7d8 
  font-size:1.5em;
  background: rgba( 111, 111, 111,1);
}
`;
const rt = css `
width:80%;
background: rgba(23, 23, 23,0.8);
& .registry-form-content{
  background: rgba(23, 23, 23,0);
  max-width:600px
  margin: 0 auto
}
`;;
class Center extends Component {
  state = {};
  render() {
    let list = [
      {
        name: '个人信息',
        tow: '/center/parsonal'
      }, {
        name: '活动管理',
        tow: '/center/avtiviyManager'
      }
    ]
    class Menu extends Component {
      static propTypes = {
        match: PropTypes.object.isRequired,
        location: PropTypes.object.isRequired,
        history: PropTypes.object.isRequired
      }

      render() {
        const {match, location, history} = this.props
        let that = this;

        return (
          <div onClick={() => history.push(that.props.tow)}>{that.props.name}</div>
        )
      }
    }
    let B = withRouter(Menu);
    const Holder = (props) => <Switch>
    <Route path="/center/parsonal" component={Personal}/>
    <Route path='/center/avtiviyManager' component={ActivityManager}/>
  </Switch>
    return <div className={content}>
      <div className={lf}>{list.map(e => <B {...e}/>)}</div>
      <div className={rt}>
        <Holder/>
      </div>
    </div>;
  }
}

export default Center;



