import React, {Component} from 'react';
import Home from './home/Home';
import {PrivateRoute} from './Auth';
import Plateform from './home/Plateform';
import Login from './login/Login';
import Registry from './login/Registry';
import Train from './plateform/TrainList';
import TrainDetail from './plateform/TrainDetail';
import News from './plateform/NewsList';
import NewsDetail from './plateform/NewsDetail';
import Quality from './plateform/QualityList';
import QualityDetail from './plateform/QualityDetail';
import Lecturer from './plateform/LecturerList';
import LecturerDetail from './plateform/LecturerDetail';
import {Switch, Route} from 'react-router-dom';
import './Main.css';

import Center from './center/Center';

class Main extends Component {
  state = {};
  setUser(user) {}
  render() {
    return (
      <Switch>
        <Route exact path="/" component={Login}/>
        <PrivateRoute exact path="/home" component={Home}/>
        <Route path="/login" component={Login}/>
        <Route path="/registry" component={Registry}/>
        <PrivateRoute path="/plateform" component={Plateform}/>
        <PrivateRoute exact path="/train" component={Train}/>
        <PrivateRoute path="/train/:id" component={TrainDetail}/>
        <PrivateRoute exact path="/news" component={News}/>
        <PrivateRoute path="/news/:id" component={NewsDetail}/>
        <PrivateRoute exact path="/quality" component={Quality}/>
        <PrivateRoute path="/quality/:id" component={QualityDetail}/>
        <PrivateRoute exact path="/lecturer" component={Lecturer}/>
        <PrivateRoute path="/lecturer/:id" component={LecturerDetail}/>

        <PrivateRoute path="/center" component={Center}/>
      </Switch>
    )
  }
}
export default Main;


