import './Deck.css';
import React, {Component} from 'react';
import DeckTitle from './DeckTitle';
import QualityCard from '../card/QualityCard';
import {Quality} from '../Api';

class QualityDeck extends Component {
  state = {
    list: Quality.qualitys

  };
  render() {
    return (
      <div className="pl-0 pt-3 pr-3 pb-3">
        <DeckTitle title="精品推荐" bTitle="more" to="/quality"/> {this
          .state
          .list
          .slice(0, 3)
          .map(element =>< QualityCard {
            ...element
          } />)}
      </div>
    );
  }
}

export default QualityDeck;



