import React, {Component} from 'react';
import {css} from 'react-emotion'
import PageTitle from '../common/PageTitle';
import FilterBox from '../common/FilterBox';
import QualityCard from '../card/QualityCard';
import {Container, Row, Col} from 'reactstrap';
import {Quality} from '../Api';

const c = css `
width: 80%;
height: 100%;
margin: 0% auto;

`
const half = css `
width:50%
`
const flex = css `
display: flex;
flex-wrap: wrap;

`
const leftContent = css `
padding: 0px 0px !important;
`
const card = css `
padding: 2% 3%;
`

const container =css`
padding: 0px 0px !important;
margin: 0px 0px !important;
& .row {
    margin: 0px;
}
`
export default class NewsIndex extends Component {
  state = {
    type: '全部',
    ctype: '全部',
    list: Quality.qualitys
  };
  render() {
    let that = this;
    const filter = [
      {
        title: '按类型',
        onSel: e => that.setState({type: e.text}),
        contentTypes: [
          {
            text: '全部',
            select: true
          }, {
            text: '会议'
          }, {
            text: '新闻'
          }, {
            text: '课程'
          }, {
            text: '通知'
          }
        ]
      }, {
        title: '按内容',
        onSel: e => that.setState({ctype: e.text}),
        contentTypes: [
          {
            text: '全部',
            select: true
          }, {
            text: '技术'
          }, {
            text: 'UED'
          }, {
            text: '产品'
          }, {
            text: '数据'
          }
        ]
      }
    ];
    return (
      <div className={c}>
        <PageTitle title="精品推荐"/>
        <Container  className={container}>
          <Row>
            <Col className={leftContent} xs="3">
              {filter.map(e => <FilterBox onSel={e.onSel} title={e.title} contentTypes={e.contentTypes}/>)}
            </Col>
            <Col className={flex} xs="9">
            {this
              .state
              .list
              .map(element =>< span className = {
                card + " " + half
              } > <QualityCard {...element}
                /> </span>)}
          </Col>
          </Row>
        </Container>
      </div>
    );
  }
}


