import React, {Component} from 'react';
import {Jumbotron} from 'reactstrap';
import {Lecturer} from '../Api';
import {css} from 'react-emotion'
const content = css `
// width:60%;
max-width:1000px;
display: flex;
margin: 0 auto;
padding: 2%;
background: rgba(233, 236, 239,1);
height:100%;
& .jumbotron{
    background: rgba(233, 236, 239, 1)
}
`

const lf = css `
width:24%;
`;
const rt = css `
width:76%;
`;

class LecturerDetail extends Component {
  constructor(props) {
    super(props);
    this.switch = this
      .switch
      .bind(this)
    this.toggle = this
      .toggle
      .bind(this)

    let id = this.props.match.params.id;
    let detail = Lecturer.get(id);
    this.state = {
      detail: detail,
      status: false,
      modal: false
    };
  }
  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  }
  switch (e) {
      this.setState({
        status: !this.state.status
      });
      this.toggle();
  }
  state = {};
  render() {
    let detail = Lecturer.get(this.props.match.params.id);
    return (
      <div className={content}>
        <div className={lf}>
          <img height="240" width="100%" src={detail.imgUrl} alt={detail.alt}/>
        </div >
        <div className={rt}>
          <Jumbotron className="pt-0">
            <h1 className="display-3 text-justify">{detail.name}</h1>
            <p className="pl-1 lead text-justify">{detail.introduction}</p>
            <p className="lead">{detail.detail}</p>
            <hr className="my-2"/>
          </Jumbotron>
        </div>
      </div>

    );
  }
}

export default LecturerDetail;



