import React, { Component } from 'react';
import { Card, Button, CardImg, CardTitle, CardBody } from 'reactstrap';
import { withRouter } from 'react-router-dom';
import { Container, Row, Col } from 'reactstrap';
import './Card.css';

class TrainCard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgUrl: this.props.imgUrl
        ? this.props.imgUrl
        : 'https://placeholdit.imgix.net/~text?txtsize=33&txt=318%c3%97180&w=318&h=90',
      color: this.props.initialColor
    };
  }
  state = {};
  render() {
    let that = this;
    const B = withRouter(({ history }) => (
      <Button onClick={() => history.push(that.props.to)}>进入活动</Button>
    ));

    return (
      <Card {...this.props}>
        <CardImg
          top
          width="100%"
          src={this.state.imgUrl}
          alt={this.state.alt}
        />
        <CardBody>
          <Container>
            <Row>
              <Col xs="12">
                <CardTitle className="text-justify">
                  {this.props.title}
                </CardTitle>
              </Col>
            </Row>
            <Row>
              <Col className="text-justify box-large p-1" xs="6">
                时间：{this.props.time}
                <br />
                地点：{this.props.place}
              </Col>
              <Col className="p-1" xs="6">
                <B />
              </Col>
            </Row>
          </Container>
        </CardBody>
      </Card>
    );
  }
}

export default TrainCard;



