import React, { Component } from 'react';
class VCode extends Component {
  state = {};
  render() {
    return (
      <img
        alt="vcode"
        style={{
          height: '38px',
          width: '87px',
          borderBottomRightRadius: '4px',
          borderTopRightRadius: '4px'
        }}
        src="data:image/jpeg;base64,"
        className="vcode-img"
      />
    );
  }
}

export default VCode;



