import React, {Component} from 'react';
import {
  CardImg,
  CardTitle,
  CardText,
  Jumbotron,
} from 'reactstrap';
import {Row, Col} from 'reactstrap';
import {Quality} from '../Api';
import {css} from 'react-emotion'
import '../card/Card.css';
import {withRouter} from 'react-router-dom'

const content = css `
// width:60%;
margin: 0 auto;
padding:2%
max-width:1000px;

background: rgba(222, 221, 221, 0.8);
& button{
    padding: 10px 37px;
    color: #cac8c8;
    border-radius: 30px;
    border: 0px;
    background: #303837;
    cursor: pointer;
}
`

const bs =css`
position: absolute;
bottom: 20%;
`
const ms =css`
position: absolute;
bottom: 50%;
`
const full = css`
height:100%;
`
const lecturer = css`
color:orange;
cursor:pointer;
`

class QualityDetail extends Component {
  constructor(props) {
    super(props);
    this.start = this
      .start
      .bind(this)

    let id = this.props.match.params.id;
    let detail = Quality.get(id);
    this.state = {
      detail: detail
    };
  }
  start(e) {}
  state = {};
  render() {
    let detail = Quality.get(this.props.match.params.id);
    let Link = withRouter(({history})=><span onClick={()=>history.push('/lecturer/'+detail.lecturerId)} className={lecturer+" pr-3 "}> {detail.lecturerName}</span>)
    return (
      <div className={content}>
        <div className="pb-3 pl-0 pr-0">
          <Row>
            <Col xs="5" className="video-thumbnail">
              <CardImg  bottom height="240" width="100%" src={detail.imgUrl} alt={detail.alt}/>
            </Col>
            <Col xs="7">
              <div className={full+" p-3"}>
                <CardTitle className="text-justify">
                  {detail.title}
                </CardTitle>
                <CardText className={ms + " text-justify"}>
                讲师：<Link/> 
                <span> 时长：{detail.mins}</span>
       
                </CardText>
                <CardText className={bs + " text-justify"}>
                  <button onClick={this.start}>开始学习</button>
                </CardText>
              </div>
            </Col>
          </Row>
        </div>
        <Jumbotron>

          <h1 className="display-3">Domain Driven Design</h1>
          <p className="lead">开放平台为广大开放商提供了一套完整的前端JSAPI，承载了社交渠道、支付营销和画布控制等功能，本课程主要介绍JSAPI的基本原理、使用方法与场景和前端研发的调试工具及常见问题。</p>
          <hr className="my-2"/>
          <img height="240" width="100%" src={detail.imgUrl} alt={detail.alt}/>
        </Jumbotron>
      </div>

    );
  }
}

export default QualityDetail;



