import './Deck.css';
import React, {Component} from 'react';
import DeckTitle from './DeckTitle';
import LecturerCard from '../card/LecturerCard';
import {CardDeck} from 'reactstrap';
import {Lecturer} from '../Api';

class LecturerDeck extends Component {
  state = {
    list: Lecturer.lecturers
  };
  render() {
    return (
      <div className="p-3">
        <DeckTitle title="讲师风采" bTitle="more" to="/lecturer"/>
        <CardDeck>
          {this
            .state
            .list.slice(0,4)
            .map(element =>< LecturerCard {...element} />)}
        </CardDeck>
      </div>
    );
  }
}

export default LecturerDeck;



