import React, {Component} from 'react';
import {Card, CardImg, CardTitle, CardText, CardBody} from 'reactstrap';
import './Card.css';
import {withRouter} from 'react-router-dom';

class NewsCard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgUrl: this.props.imgUrl
        ? this.props.imgUrl
        : 'https://placeholdit.imgix.net/~text?txtsize=33&txt=318%c3%97180&w=318&h=250',
      color: this.props.initialColor
    };
  }
  state = {};
  render() {
    let that = this;
    let Holder = (withRouter(({history}) => (
        <Card onClick={() => history.push(that.props.to)}>
          <CardImg top width="100%" src={this.state.imgUrl} alt={this.state.alt}/>
          <CardBody>
            <CardTitle className="text-justify">{that.props.name}</CardTitle>
            <CardText className="text-justify">{that.props.introduction}</CardText>
          </CardBody>
        </Card>
      )))
    return <Holder/>;
  }
}

export default NewsCard;



