import React, {Component} from 'react';
import {Card, CardImg, CardTitle, CardText, CardBody} from 'reactstrap';
import {Container, Row, Col} from 'reactstrap';
import './Card.css';
import {withRouter} from 'react-router-dom';

class NewsCard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgUrl: this.props.imgUrl
        ? this.props.imgUrl
        : 'https://placeholdit.imgix.net/~text?txtsize=33&txt=318%c3%97180&w=80&h=91',
      color: this.props.initialColor
    };
  }
  state = {};
  render() {
    let that = this;
    let Holder = (withRouter(({history}) => (
        <Card onClick={()=>history.push(that.props.to)} className="mt-3 mb-3">
          <CardBody>
            <Container>
              <Row>
                <Col xs="3">
                  <CardImg bottom width="100%" src={this.state.imgUrl} alt={this.state.alt}/>
                </Col>
                <Col xs="9">
                  <div className="p-3">
                    <CardTitle className="text-justify">
                      {that.props.name}
                    </CardTitle>
                    <CardText className="text-justify">
                      {that.props.detail}
                    </CardText>
                  </div>
                </Col>
              </Row>
            </Container>
          </CardBody>
        </Card>
      )))
    return <Holder/>;
  }
}

export default NewsCard;



