import React, {Component} from 'react';
import {css} from 'react-emotion'
import FilterBox from '../common/FilterBox';
import ActiviyCalendar from '../common/ActiviyCalendar';
import TrainCard from '../card/TrainCard';
import {Train} from '../Api';
import {
  Container,
  Row,
  Col
} from 'reactstrap';
const c = css `
height: 100%;
margin: 0% auto;
overflow: overlay;
`
const half = css `
width:50%
`
const flex = css `
display: flex;
flex-wrap: wrap;


`
const leftContent = css `
padding: 2% 0px !important;
`
const card = css `
padding: 2% 3%;
`
class ActivityManager extends Component {
  state = {
    type: '全部',
    ctype: '全部',
    date: new Date(),
    list: Train.trains
  };
  render() {
    let that = this;
    const filter = [
      {
        title: '按类型',
        onSel: e => that.setState({type: e.text}),
        contentTypes: [
          {
            text: '全部',
            select: true
          }, {
            text: '会议'
          }, {
            text: '新闻'
          }, {
            text: '课程'
          }, {
            text: '通知'
          }
        ]
      }, {
        title: '按内容',
        onSel: e => that.setState({ctype: e.text}),
        contentTypes: [
          {
            text: '全部',
            select: true
          }, {
            text: '技术'
          }, {
            text: 'UED'
          }, {
            text: '产品'
          }, {
            text: '数据'
          }
        ]
      }
    ];
  

    return (
      <div className={c}>
        <Container>
          <Row>
            <Col className={leftContent} xs="3">
              < ActiviyCalendar onChange={date => that.setState({date: date})}/> {filter.map(e => <FilterBox onSel={e.onSel} title={e.title} contentTypes={e.contentTypes}/>)}
            </Col>
            <Col className={flex} xs="9">
              {this
                .state
                .list
                .map(element =>< span className = {
                  card + " " + half
                } > <TrainCard {...element}/> </span>)}
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}
export default ActivityManager;



