import React, { Component } from 'react';
import IndexDeck from '../deck/IndexDeck';
import Bulletin from '../deck/Bulletin';
class Home extends Component {
  state = {};
  render() {
    return (
      <div>
        <div className="bulletin-deck">
          <Bulletin />
        </div>
        <IndexDeck />
      </div>
    );
  }
}

export default Home;



