import React, {Component} from 'react';
import {css} from 'react-emotion'
import {Button} from 'reactstrap';
const header = css `
color: #d6d7d8 !important;
`
const typebox = css `
line-height:50px;
display: flex;
line-height: 50px;
flex-wrap: wrap;
`
const type = css `
color: #cecbcb;
margin: 5px 0px;
border-color: #fff;
cursor: pointer;
line-height: initial;
width: 33%;
padding: 5px 0px;
display: inline-block;
&:hover{
    background-color: #4e8bc1;
}
`
const select = css `
background-color: #bd7c05;
`
class FilterBox extends Component {
  constructor(props) {
    super(props);
    this.handerClick = this.handerClick.bind(this);
    this.state = {
      contentTypes: this.props.contentTypes.map((e, i) => {
        e.id = "FilterBox-" + i
        e.index = i
        return e;
      })
    };
    
  }
  handerClick = (e) => {
    let index = e.currentTarget.getAttribute('index')
    this.setState({
        contentTypes: this.props.contentTypes.map((e, i) => {
            e.id = "FilterBox-" + i
            e.index = i
            e.select = index == i
            return e;
          })
    })
    if (this.props.onSel) {
      this
        .props
        .onSel(this.props.contentTypes[index])
    }
  };
  render() {
      let that = this;
    return (
      <div>
        <div className={header + " h5 text-justify pt-3"}>{this.props.title}</div>
        <div className={typebox}>
          {this
        .state
        .contentTypes
        .map((e, i) => {
          return <span
            onClick={that.handerClick}
            className={e.select
            ? select + " " + type
            : type}
            {...e}>{e.text}</span>
        })}
        </div>
      </div>
    );
  }
}

export default FilterBox;


