import './Deck.css';
import React, {Component} from 'react';
import DeckTitle from './DeckTitle';
import TrainCard from '../card/TrainCard';
import {CardDeck} from 'reactstrap';
import {Train} from '../Api';

class TrainDeck extends Component {
  state = {};
  render() {
    const a = () => Train
      .trains
      .slice(0, 3)
      .map(train => (<TrainCard {...train}/>));
    return (
      <div className="p-3">
        <DeckTitle title="培训报名" subTitle="text" bTitle="more" to="/train"/>
        <CardDeck>{a()}</CardDeck>
      </div>
    );
  }
}

export default TrainDeck;



